                    {
                         name:"Mobile Arcade Game",
                         description:"Implemented the back-end of this game using Unity3d and C#. The main tasks were Authentication using Google Play and Game Center, Database, and monetization plans using Admob and In-app purchases. It will be published on Android and IOS in the near future."
                    },
                    {
                         name:"Property Manager",
                         description:"Implemented a full-stack application as a CRUD service for properties using .NET Core, Angular 8, and Angular Material."
                    },
                    {
                         name:"Home Automation & Security System",
                         description:"Designed and built this project for an Internet-of-Things class using Raspberry Pi and Python. The main functionalities were face detection (using OpenCV) to show intruders, remote control from the phone using the LAMP stack, python's HTTP server on the product-end, LCD screen for display, RFID sensor for authentication, and a machine learning model for predicting plant watering according to various weather data from sensors (using KNN and SVM)."
                    },
                    {
                         name:"3D Fighting Game",
                         description:"Developed this project for a Game Programming class using Unity and C#. Some of the main features were an SQL database, game settings and audio, AI for the enemy, and the difficulties with the proper animations and graphics."
                    },
                    {
                         name:"HTTP Proxy Server",
                         description:"Built a fully functional multi-threaded HTTP web proxy server using C for a system programming class. The project was tested to act as a local proxy on the web browser. The product acted as a cache system that hugely improved the loading time of websites and simplified the communication with the server."
                    },
                    {
                         name:"Security-related Projects",
