<template>
     <div class="contact mx-16">
          <h3>
               Whether you want to get in touch or you're searching for top notch software development and data analytics consultants/teams, 
               do not hesitate to fill this form!
          </h3>
          <ValidationObserver ref="observer" v-slot="{ validate, reset }">
               <form class="ma-10">
                    <ValidationProvider v-slot="{ errors }" name="Name" rules="required">
                         <v-text-field
                              v-model="name"
                              :error-messages="errors"
                              label="Name*"
                              required
                         ></v-text-field>
                    </ValidationProvider>
                    <ValidationProvider v-slot="{ errors }" name="Email" rules="required|email">
                         <v-text-field
                              v-model="email"
                              :error-messages="errors"
                              label="E-mail*"
                              required
                         ></v-text-field>
                    </ValidationProvider>
                     <ValidationProvider v-slot="{ errors }" name="Message" rules="required">
                         <v-textarea
                              v-model="message"
                              :error-messages="errors"
                              label="Message*"
                              required
                         ></v-textarea>
                    </ValidationProvider>
                    <v-btn 
                         class="mr-4 mt-4 primary"
                         @click="sendEmail"
                         :disabled="!name || !email || !message"
                    >
                         Send
                    </v-btn>
               </form>
          </ValidationObserver>
     </div>
</template>

<script>
import { required, email, max } from 'vee-validate/dist/rules'
import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'

setInteractionMode('eager')

extend('required', {
    ...required,
    message: '{_field_} can not be empty',
})

extend('email', {
    ...email,
    message: 'Email must be valid',
})

export default {
     components: {
          ValidationProvider,
          ValidationObserver,
     },
     data: () => ({
          name: '',
          email: '',
          message: '',
     }),

     methods: {
          sendEmail () {
               let valid = this.$refs.observer.validate();
               console.log('sending email');
               this.$emit('changeLoader', true);
               valid.then(()=>{
                    if(valid){
                              this.$http.post('https://formspree.io/mzbjbbgo',{
                                   name: this.name,          
                                   from: this.email,          
                                   _subject: `${this.name} | Message From Your Website`,
                                   message: this.message,
                              })
                              .then((response) => {
                                   console.log(response)
                                   this.$snotify.success('Email Sent!', 'Success');
                                   this.clear();
                              }).catch((error) => { 
                                   if (error.response) {  
                                        console.log(error.response);
                                        this.$snotify.error('Please retry!', 'Error');   
                                   }
                              }).finally(() => {
                                   this.$emit('changeLoader', false);
                              });
                    }
               });
          },
          clear () {
               this.name = '';
               this.email = '';
               this.message = '';
               this.$refs.observer.reset();
          },
     },
}
</script>

<style scoped>
.contact{
     padding-bottom: 10px;
}
h3{
     font-size: 20px;
     text-align: center;
     font-style: italic;
     padding-left: 10px;
     padding-right: 10px;
}
</style>