                         'image':"lau.jpg",
                         'link':"https://www.lau.edu.lb/",
                         'courseList':"http://sas.lau.edu.lb/csm/programs/bs-computer-science.php"
                    }
               ],
          }
     }
}
</script>

<style scoped>
.word_break{
